import RiveCanvas, { File, Artboard, StateMachineInstance, LinearAnimationInstance, SMIInput, FileAsset, FileAssetLoader, FontAsset } from "@rive-app/canvas-advanced";
import { Rive, decodeFont } from "@rive-app/canvas";
// import "style.css";

interface TileInstanceData {
  x: number;
  y: number;
  artboard: Artboard;
  machine: StateMachineInstance;
  anim: LinearAnimationInstance;
  anim2: LinearAnimationInstance;
  tapTrigger: SMIInput;
}

async function main() {
  const rive = await RiveCanvas({
    // Loads Wasm bundle
    locateFile: (_) =>
      `https://unpkg.com/@rive-app/canvas-advanced@2.7.6/rive.wasm`,
  });
  const canvas = document.getElementById("rive-canvas") as HTMLCanvasElement;
  canvas.height = document.documentElement.clientHeight;
  canvas.width = document.documentElement.clientWidth;

  const scaleX = canvas.width / 1792;
  const scaleY = canvas.height / 922;

  const padX = 25 * scaleX;
  const padY = 10 * scaleY;

  const dim = (canvas.height - padY * 6) / 5;
  // const bgWidth = canvas.width - (padX + dim) * 2;
  const bgWidth = canvas.width;
  
  const fontbytes = await (
    await fetch(new Request("Montserrat-594893.ttf"))
  ).arrayBuffer();
  
  // const font = (await rive.load(new Uint8Array(fontbytes))) as FontAsset;
  

  const renderer = rive.makeRenderer(canvas);
  const bytes = await (
    // await fetch(new Request("2023-10-23-tile_test-001.riv"))
    await fetch(new Request("Button.riv"))
    // await fetch(new Request("Button.riv"))
  ).arrayBuffer();
  const file = (await rive.load(new Uint8Array(bytes))) as File;
  const artboardG = file.defaultArtboard();

  const buttonMachine = new rive.StateMachineInstance(
    artboardG.stateMachineByName("State Machine"),
    artboardG
  );

  var buttonMachineColor;

  for (let i = 0, l = buttonMachine.inputCount(); i < l; i++) {
    const input = buttonMachine.input(i);
    // console.log(input.name, input.type)
    // console.log(input, input.name, input.type, input.asNumber().value)
    if (input.name == "Color") {
      buttonMachineColor = input;
    } 
  }
  
  const timerBytes = await (
    await fetch(new Request("Timer.riv"))
  ).arrayBuffer();
  const timerFile = (await rive.load(new Uint8Array(timerBytes))) as File;
  const artboardTimer = timerFile.defaultArtboard();

  const timerInfo = { x: 0, y: 0, w: 0, h: 0};

  timerInfo.w = 300 * scaleX;
  timerInfo.h = 200 * scaleX;
  timerInfo.x = canvas.width / 2 - timerInfo.w / 2;
  timerInfo.y = 0;

  const timerMachine = new rive.StateMachineInstance(
    artboardTimer.stateMachineByName("State Machine"),
    artboardTimer
  );

  const gameDuration = 25;
  var timerT = gameDuration;
  var timerMachineProgress;
  var timerMachineUrgent;

  for (let i = 0, l = timerMachine.inputCount(); i < l; i++) {
    const input = timerMachine.input(i);
    // console.log(input.name, input.type)
    // console.log(input, input.name, input.type, input.asNumber().value)
    if (input.name == "Timer") {
      timerMachineProgress = input;
    } else if (input.name == "Urgent") {
      timerMachineUrgent = input;
    } 
  }

  // for (let i = 0; i < artboardTimer.animationCount(); ++i) {
  //   console.log(artboardTimer.animationByIndex(i).name);
  //   // if (artboardG.animationByIndex(i).name == 'Tap / Correct') {
  //   //   console.log('test')
  //   // }
  // }

  const bytes2 = await (
    await fetch(new Request("Background.riv"))
    // await fetch(new Request("2023-10-26-Background-Test-001.riv"))
    // await fetch(new Request("2023-10-25-Background-Test-001.riv"))
    // await fetch(new Request("2023-10-23-Background-Test-001.riv"))
  ).arrayBuffer();
  const file2 = (await rive.load(new Uint8Array(bytes2))) as File;
  const artboardBG = file2.defaultArtboard();


  let bgAnim = new rive.LinearAnimationInstance(
    // artboardBG.animationByIndex(0),
    artboardBG.animationByName("Tap"),
    artboardBG
  );

  let bgAnimBuoyA = new rive.LinearAnimationInstance(
    // artboardBG.animationByIndex(0),
    artboardBG.animationByName("Bouy A"),
    artboardBG
  );

  let bgAnimPop = new rive.LinearAnimationInstance(
    // artboardBG.animationByIndex(1),
    artboardBG.animationByName("Pop"),
    artboardBG
  );

  // console.log(bgAnimPop);
  
  let bgAnimPlane = new rive.LinearAnimationInstance(
    // artboardBG.animationByIndex(0),
    artboardBG.animationByName("Plane Flying"),
    artboardBG
  );

  const bgMachine = new rive.StateMachineInstance(
    artboardBG.stateMachineByName("State Machine"),
    artboardBG
  );

  const progressDuration = 2;
  var balloonRotationT = 0;

  // console.log(artboardBG);

  // for (let i = 0; i < artboardBG.animationCount(); ++i) {
  //     console.log(artboardBG.animationByIndex(i).name);
  //     // if (artboardG.animationByIndex(i).name == 'Tap / Correct') {
  //     //   console.log('test')
  //     // }
  // }
  // console.log(bgAnim);
  let progressT = 0;
  let bgMachineProgress;
  let bgMachineTileSide;
  let bgMachineBalloonX;
  let bgMachineBalloonRotation;
  let bgMachineColor;
  let bgMachineTap;
  let bgMachinePop;
  let balloonDirection = 1;
  let balloonX = 0;
  let balloonRotation = 0;
  for (let i = 0, l = bgMachine.inputCount(); i < l; i++) {
    const input = bgMachine.input(i);
    // console.log(input.name, input.type)
    // console.log(input, input.name, input.type, input.asNumber().value)
    if (input.name == "Progress") {
      bgMachineProgress = input;
    } else if (input.name == "TileSide") {
      bgMachineTileSide = input;
    } else if (input.name == "BalloonX") {
      bgMachineBalloonX = input;
    }  else if (input.name == "BalloonRotation") {
      bgMachineBalloonRotation = input;
    } else if (input.name == "Color") {
      bgMachineColor = input;
    } else if (input.name == "Tap") {
      bgMachineTap = input;
      // console.log(bgMachineTap)
    } else if (input.name == "Pop") {
      bgMachinePop = input.asTrigger();
      // console.log(bgMachineTap)
    }
      
  }

  let colorRng = Math.floor(Math.random() * 6);
  bgMachineColor.asNumber().value = colorRng;
  buttonMachineColor.asNumber().value = colorRng;

  // bgMachine.advance(0);

  const tileNums = [] as any;
  const tileNumsT = [] as any;
  var currTile = -1;
  var currNum = 1;
  var topNum = 10;
  var isCorrect = false;
  var tileAnim = [] as any;

  function initTiles() {
    for (let i = 0; i < 10; ++i) {
      tileNums[i] = i + 1;
      tileNumsT[i] = tileNums[i];
      tileAnim[i] = {
        type: 0,
        t: 0
      };
    }
  
    shuffleArr(tileNums);
  }

  initTiles();

  let lastTime = 0;
  // console.log(artboard);
  const tile = file.artboardByName("artboard");
  const tiles = new Set<TileInstanceData>();
  // console.log(tile);
  spawnTiles();
  // spawnTiles();

  window.addEventListener('resize', (e) => {
    location.href = '';
  });

  let mDown = false;
  canvas.addEventListener('mousedown', (e) => {
    let mx = e.offsetX;
    let my = e.offsetY;

    if (!mDown) {
      mDown = true;
      currTile = isTileClicked(mx, my);

      if (currTile > -1) {
        if (tileAnim[currTile].t == 0) {
          if (currNum == tileNums[currTile]) {
            isCorrect = true;
            currNum++;
            
            // bgMachineTileSide.asNumber().value += 100;
            bgMachineTap.asTrigger().fire();
            
            // tileNums[currTile] = ++topNum;
            tileNumsT[currTile] = ++topNum;
            tileAnim[currTile].type = 1;
            tileAnim[currTile].t = 2;
            progressT = progressDuration;
            balloonRotationT = 0;

            if (currTile < 5) {
              bgMachineTileSide.asNumber().value = 0;
              balloonDirection = 1;
            } else {
              bgMachineTileSide.asNumber().value = 1;
              balloonDirection = -1;
            }
          } else {
            isCorrect = false;
            tileAnim[currTile].type = 2;
            tileAnim[currTile].t = 2;

            // bgMachinePop.fire();
          }
        }
        
      }
    }
  });

  canvas.addEventListener('mouseup', (e) => {
    if (mDown) {
      mDown = false;
    }
  });

  function spawnTiles() {
    for (let i = 0; i < 2; ++i) {
      for (let j = 0; j < 5; ++j) {
        const tileInstance = file.artboardByName("artboard");
        const tileMachine = new rive.StateMachineInstance(
          tile.stateMachineByName("State Machine"),
          tileInstance
        );
        
        let tapTrigger;

        for (let i = 0, l = tileMachine.inputCount(); i < l; i++) {
          const input = tileMachine.input(i);
          // console.log(input.name)
          switch (input.name) {
            case "Tap":
              tapTrigger = input.asTrigger();
              break;
  
            default:
              break;
          }
        }
        let x = i ? canvas.width - (dim + padX) : padX;

        let anim = new rive.LinearAnimationInstance(
          tileInstance.animationByName("Tap / Correct"),
          tileInstance
        );

        // tapTrigger.fire();

        // console.log(anim, tileInstance.animationByName("Tap / Correct"))
        // anim.advance(0);
        // anim.apply(0);

        let anim2 = new rive.LinearAnimationInstance(
          tileInstance.animationByName("Tap / Incorrect"),
          tileInstance
        );

        const blockInstance = {
          x: x,
          y: j * dim + padY * (j + 1),
          artboard: tileInstance,
          machine: tileMachine,
          anim: anim,
          anim2: anim2,
          tapTrigger: tapTrigger
        };
  
        tileMachine.advance(0);
        blockInstance.artboard.advance(0);
  
        tiles.add(blockInstance);
      }
    }
  }

  function isTileClicked(mx, my) {
    for (let i = 0; i < 2; ++i) {
      for (let j = 0; j < 5; ++j) {
        let x = i ? canvas.width - (dim + padX) : padX;
        let y = j * dim + padY * (j + 1);
        if (mx >= x && mx <= x + dim && my >= y && my <= y + dim) {
          return i * 5 + j;
        }
      }
    }

    return -1;
  }

  function shuffleArr(array) {
    for (var i = array.length - 1; i > 0; i--) {
        var rand = Math.floor(Math.random() * (i + 1));
        [array[i], array[rand]] = [array[rand], array[i]]
    }
  }

  function lerp(a, b, t) {
    return a + (b - a) * t;
  }

  function renderLoop(time) {
    if (!lastTime) {
      lastTime = time;
    }
    const elapsedTimeMs = time - lastTime;
    const elapsedTimeSec = elapsedTimeMs / 1000;
    lastTime = time;

    renderer.clear();
    renderer.save();

    if (artboardG && artboardBG) {
      let idx = 0;
      // if (bgAnimPlane) {
      //   bgAnimPlane.advance(elapsedTimeSec);
      //   bgAnimPlane.apply(1);
      // }
      
      // if (bgAnimBuoyA) {
      //   bgAnimBuoyA.advance(elapsedTimeSec);
      //   bgAnimBuoyA.apply(1);
      // }
      
      // if (bgAnimPop) {
      //   bgMachineProgress.asNumber().value = 0;
      //   bgAnimPop.advance(elapsedTimeSec);
      //   bgAnimPop.apply(1);
      // }

      // for (let i = 0, l = bgMachine.inputCount(); i < l; i++) {
      //   const input = bgMachine.input(i);
        
      //   // console.log(input, input.name, input.type, input.asNumber().value)
      //   input.asNumber().value += elapsedTimeSec;
      // }
      // 
      if (progressT > 0) {
        progressT -= 1 * elapsedTimeSec;
        let val = 1 * progressT;
        bgMachineProgress.asNumber().value += val * 0.25;
        
        balloonX += balloonDirection * val;
        
        if (Math.abs(balloonX) >= 100) {
          balloonX = 100 * balloonDirection;
        }

        balloonRotation = balloonDirection * 100;
        // if (Math.abs(balloonRotation) >= 100) {
        //   balloonRotation = 100 * balloonDirection;
        // }
        console.log(balloonRotation)

        bgMachineBalloonX.asNumber().value = balloonX;
        bgMachineBalloonRotation.asNumber().value = balloonRotation;
        
        if (progressT <= 0) {
          progressT = 0;
        }
      } else {
        
      }

      if (balloonRotation != 0) {
        // console.log(balloonRotation)
        balloonRotation = lerp(balloonDirection * 100, 0, balloonRotationT);
        
        bgMachineBalloonRotation.asNumber().value = balloonRotation;
        balloonRotationT += 1 * elapsedTimeSec;
        // console.log(elapsedTimeMs)
        if (balloonRotationT > 1) balloonRotationT = 1;
      }

      // bgMachineBalloonX.asNumber().value += 1000 * elapsedTimeSec;

      bgMachine.advance(elapsedTimeSec);
      // bgMachine.apply(1);
      

      renderer.save();
      // let x = padX + dim;
      let x = 0;
      let y = 0;
      renderer.translate(x, y);
      renderer.align(
        rive.Fit.fitWidth,
        rive.Alignment.center,
        {
          minX: 0,
          minY: 0,
          maxX: bgWidth,
          maxY: canvas.height
        },
        artboardBG.bounds
      );
      artboardBG.advance(elapsedTimeSec);
      // artboardBG.apply(elapsedTimeSec);
      // artboardBG.apply(elapsedTimeSec);
      artboardBG.draw(renderer);
      
      renderer.restore();

      for (let blockInstance of Array.from(tiles)) {
        const { artboard, machine, anim, anim2, tapTrigger, x, y } = blockInstance;

        renderer.save();

        if (tileAnim[idx].t > 0) {
          if (tileAnim[idx].type == 1) {
            anim.advance(elapsedTimeSec);
            anim.apply(1);
          } else if (tileAnim[idx].type == 2) {
            anim2.advance(elapsedTimeSec);
            anim2.apply(1);

            // bgAnimPop.advance(elapsedTimeSec);
            // bgAnimPop.apply(1);
          }

          tileAnim[idx].t -= 1 * elapsedTimeSec;
          if (tileAnim[idx].t <= 0) {
            tileAnim[idx].t = 0;
            tileAnim[idx].type = 0;
            anim.time = 0;
            anim2.time = 0;
            
          } else if (tileAnim[idx].t <= 1) {
            if (tileAnim[idx].type == 1) {
              tileNums[idx] = tileNumsT[idx];
            }
          }
        }

        var textRun = artboard.textRun("Count");
        textRun.text = tileNums[idx].toString();
        // console.log(textRun.text)
        renderer.translate(x, y);
        renderer.align(
          rive.Fit.contain,
          rive.Alignment.center,
          {
            minX: 0,
            minY: 0,
            maxX: dim,
            maxY: dim
          },
          artboard.bounds
        );

        // tapTrigger.fire();
        
        artboard.advance(elapsedTimeSec);
        artboard.draw(renderer);
        renderer.restore();
        idx++;
      }
    }

    renderer.restore();

    if (timerT > 0) {
      timerT -= elapsedTimeSec;

      if (timerT < 0) {
        timerT = 0;
        bgMachinePop.fire();
        setTimeout(() => {
          bgMachineBalloonX.asNumber().value = 0;
          timerT = gameDuration;
          timerMachineUrgent.asBool().value = false;
          var textRun = artboardTimer.textRun("TimeLeft");
          textRun.text = gameDuration.toString();

          initTiles();

          currTile = -1;
          currNum = 1;
          topNum = 10;

          for (let blockInstance of Array.from(tiles)) {
            const { artboard, machine, anim, anim2, tapTrigger, x, y } = blockInstance;
            anim.advance(0);
            anim2.advance(0);
            // anim.apply(1);
          }
        }, 1000)
      }
      let currT = Math.floor(timerT);

      if (!timerMachineUrgent.asBool().value && currT < 11) {
        timerMachineUrgent.asBool().value = true;
      }

      let percent = timerT / gameDuration * 100;
      timerMachineProgress.asNumber().value = percent;
      // console.log(timerMachineProgress.asNumber().value)
      var textRun = artboardTimer.textRun("TimeLeft");
      textRun.text = currT.toString();
    }

    timerMachine.advance(elapsedTimeSec);

    // if (timerAnim) {
    //   timerAnim.advance(elapsedTimeSec);
    //   timerAnim.apply(1);
    // }

    // timer
    renderer.save();
    renderer.translate(timerInfo.x, timerInfo.y);
    renderer.align(
      rive.Fit.contain,
      rive.Alignment.center,
      {
        minX: 0,
        minY: 0,
        maxX: timerInfo.w,
        maxY: timerInfo.h
      },
      artboardTimer.bounds
    );
    artboardTimer.advance(elapsedTimeSec);
    

    artboardTimer.draw(renderer);
    renderer.restore();

    
    rive.requestAnimationFrame(renderLoop);
  }
  rive.requestAnimationFrame(renderLoop);
}

main();
